header{
    display: block;
    justify-content: center;
    align-items: center;
    box-shadow: 2px 2px 5px #999;
}

.oferta{
    background-color: #2c387e;
    color:white;
    height: 45px;
    font-size: 20px;
    text-align: center;
}
.nav{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    padding-right: 20px;
}
.nav ul{
    display: flex;
}
li{
    list-style: none;
    padding-left: 1rem;
    padding-right: 1rem;
}
b{
    font-weight: bold;
    color: #fff700 !important;
    font-family: Arial, Helvetica, sans-serif;
}
@media only screen and (max-width:660px){
    header{
        text-align: center;
        align-items: center;
    }
    .nav {
        display: block;
        width: 100%;
    }
    .oferta{
        background-color: #0D2EFE;
        color:white;
        width: 100%;
        height: 100%;
        font-size: 20px;
        text-align: center;
    }
    
}